<template>
  <div class="panel" :id="name">
    <div class="panel-heading" @click="unfold">
      <div class="title">
        <slot name="icon"></slot>
        <span>
          <slot name="title"></slot>
        </span>
      </div>
      <i class="fas fa-angle-down icon"></i>
    </div>

    <div class="panel-body">
      <div class="expanded">
        <slot name="content"></slot>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Panel",
    props:{
      name:String,
      expanded:{
        type:Number,
        default:150
      }
    },
    data(){
      return{
        active:false,
        panelName:this.name,
      }
    },
    methods:{
      unfold(){
        /* 1）遍历所有兄弟panel，关掉所有展开状态下的panel组件
        *  2）展开用户点击到的panel组件
        *  3）注意点：阻止用户频繁点击同一个组件，这会导致样式失控 */

        // 第一步
        //let panelGroup = document.querySelector('.accordion .panel-group').childNodes;
        let thisPanel = document.querySelector(`#${this.name}`);
        let panelHead = document.querySelector(`#${this.name} .panel-heading`);
        let panelBody = document.querySelector(`#${this.name} .panel-body`);

        let panelGroup = thisPanel.parentNode.childNodes;

        panelGroup.forEach(panel => {
          if(panel.classList.contains('active') && !panel.isSameNode(thisPanel)){
            panel.classList.remove('active');
            let pbody = panel.lastChild;
            pbody.style.height = null;
            pbody.style.display = 'block';
          }
        });

        // 第二步
        if (thisPanel.classList.contains('active')){
          // 如果用户点击的panel处于激活状态
          console.log('第二次');
          panelBody.style.height = null;
          thisPanel.classList.remove('active');
        } else {
          // 如果用户点击的panel处于未激活状态
          /* 注意：当用户频繁点击同一个panel时，第三次点击时panel的active已经被移除 */
          thisPanel.classList.add('active');
          panelBody.style.height = this.expanded + 'px';
        }
      }
    }
  }
</script>

<style scoped lang="less">
  .panel{
    width: 100%;
    border-bottom: @color-border solid 1px;

    .panel-heading{
      width: 100%;
      padding: 10px 15px;
      display: flex;
      justify-content: space-between;
      background: @color-white;
      transition: opacity 0.5s;
      cursor: pointer;
      .title{
        i{
          padding-right: 5px;
        }
      }
      .unfold{
        font-size: 18px;
        font-weight: bold;
        cursor: pointer;
      }
    }

    .panel-body{
      width: 100%;
      height: 0;
      //padding: 0 15px;
      overflow: hidden;
      background: #F8C29B;
      color: @color-white;
      transition: height 0.3s ease;
      display: block;
      .expanded{
        width: 100%;
        height: 100%;
        display: flex;
        //padding: 10px 0;
        align-items: center;
        word-wrap:break-word;
        word-break:break-all;
      }
    }

    &.active{
      .panel-heading{
        background: @color-pink;
        font-weight: bold;
        color: @color-white;
        opacity: 0.8;
        .icon{
          transform: rotate(180deg)translateY(5px);
          transition: transform 0.5s;
        }
      }
    }
  }

</style>